<script>
import Header from "../../components/global/header/HeaderArea.vue"
import { ref, onMounted, watch } from 'vue'
import { apiGetCourseInfo } from "@/api/api"
import { useRouter } from "vue-router"
import { getUrlId } from "@/utils/handle"

export default {
    setup() {
        const router = useRouter()
        let courseData = ref([])
        let urlId = getUrlId().urlId

        onMounted(async () => {
            courseData.value = await apiGetCourseInfo({ id: urlId })
            courseData.value = courseData.value.data

            courseData.value.dateInfo = `${splitDate(courseData.value.startDate)[0]} ~ ${splitDate(courseData.value.endDate)[0]}`
            courseData.value.classInfo = []
            courseData.value.courseClass.forEach(item => {
                courseData.value.classInfo.push(item.name)
            })
            courseData.value.classInfo = courseData.value.classInfo.join('\n')

            // console.log(courseData.value)
        })

        const splitDate = (val) => {
            let array = val.split(" ")
            return array
        }

        const goto = (val) => {
            router.push({ path: val })
        }

        return { courseData, urlId, goto };
    },
    components: {
        Header
    }
};
</script>

<template>
    <Header title="課程內容" backToPath="-1" />
    <div class="newCourse">
        <section class="c-main">
            <div class="detail-container">
                <div class="coursedetail-container">
                    <div class="title">{{ courseData.name }}</div>
                </div>
                <div class="item-container">
                    <div class="item-content">
                        <div class="item-data">
                            <div class="d-flex mb-1">
                                <div class="col-left">培訓單位</div>
                                <div class="col-right">{{ courseData.adviser }}</div>
                            </div>
                            <div class="d-flex mb-1">
                                <div class="col-left">課程類型</div>
                                <div class="col-right">{{ courseData.courseType }}</div>
                            </div>
                            <div class="d-flex mb-1">
                                <div class="col-left">課程名額</div>
                                <div class="col-right">{{ courseData.quota }}</div>
                            </div>
                            <div class="d-flex mb-1">
                                <div class="col-left">課程日期</div>
                                <div class="col-right">{{ courseData.dateInfo }}</div>
                            </div>
                            <div class="d-flex mb-1">
                                <div class="col-left">課程班別</div>
                                <div class="col-right" style="white-space: pre-wrap">{{ courseData.classInfo }}</div>
                            </div>
                            <div class="d-flex mb-1">
                                <div class="col-left">訓練時數</div>
                                <div class="col-right">{{ courseData.hours }}</div>
                            </div>
                            <div class="d-flex mb-1">
                                <div class="col-left">課程費用</div>
                                <div class="col-right">NT$ {{ courseData.registrationFee }}</div>
                            </div>
                            <div class="d-flex mb-1">
                                <div class="col-left">上課地點</div>
                                <div class="col-right">{{ courseData.address }}</div>
                            </div>
                        </div>
                        <div class="item-data">
                            <div class="subtitle">課程說明</div>
                            <p class="text">{{ courseData.outline }}</p>
                        </div>
                    </div>
                </div>
                <div class="mt-4b btn-container">
                    <div class="row">
                        <div class="col">
                            <button class="btn btn-outline-primary" type="button"
                                @click="goto(`/course/apply/${urlId}`)">我要報名</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.newCourse {
    margin-top: $header-height;
    margin-bottom: calc($footer-height + 15px);
}
</style>